        const { onfeedNetworkfilterApply, ontypeFilterApply, onlocationfilterApply, onOtherFilterApply, onUgcrightsFilterApply, feedNetworkfilter, typeFilter, locationfilter, otherFilter, ugcrightsFilter, filterBody, collections, networks } = this.props
        console.log("collections", collections)

